#about {
  background: $secondaryColor;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  padding: 3rem $gutterWidth;

  .profile-picture {
    margin-top: 1rem;
    width: 250px;
    height: 250px;
    border-radius: 50%; }

  .personale-desc {
    font-size: 1rem;
    line-height: 1.3rem;
    letter-spacing: 1.2rem;
    padding-top: 2rem; } }

@media screen and (min-width: 48rem) {
  #about {
    flex-direction: row !important;
    .profile-picture {
      flex-basis: 100%; }

    .personal-desc {
      padding-left: $gutterWidth; } } }
