.main-header {
  background: url("../img/background.svg");
  background-position: center center;
  background-size: cover;
  background-repeat: no-repeat;
  height: 100vh; }
  .main-header .header-text {
    color: #fff;
    text-align: center;
    display: flex;
    flex-direction: column;
    height: 100%;
    padding: 1.5rem 2rem;
    margin: 0 auto; }
    .main-header .header-text .main-title {
      font-size: 2.3rem;
      letter-spacing: 1.5px;
      text-transform: uppercase;
      line-height: 3rem;
      margin-top: 10vh; }
    .main-header .header-text .intro {
      font-size: 1.2rem;
      line-height: 1.7rem;
      letter-spacing: 1.5px; }

@media screen and (min-width: 30rem) {
  .header-text {
    width: 80% !important; }
    .header-text .main-title {
      font-size: 2.7rem !important;
      line-height: 3.2rem !important; }
    .header-text .intro {
      font-size: 1.3rem !important;
      line-height: 1.8rem !important; } }

@media screen and (min-width: 48rem) {
  .header-text {
    width: 70% !important; }
    .header-text .main-title {
      font-size: 3.2rem !important;
      line-height: 3.7rem !important; }
    .header-text .intro {
      font-size: 1.5rem !important;
      line-height: 2rem !important; } }

@media screen and (min-width: 60rem) {
  .main-title {
    font-size: 4.3rem !important;
    line-height: 4.8rem !important; }
  .intro {
    font-size: 2.5rem !important;
    line-height: 3rem !important; } }

@media screen and (min-width: 63rem) {
  .intro {
    font-size: 3rem !important;
    line-height: 3.5rem !important; } }

.navbar {
  background: trasparent;
  padding: 1.5rem 2rem; }

.nav-flex {
  display: flex;
  align-items: center; }
  .nav-flex .nav-link {
    text-decoration: none;
    color: #fff;
    padding: 10px 15px;
    margin: 0 15px;
    border-radius: 25px;
    letter-spacing: 1.3px;
    border: 1px solid #fff;
    transition: all 500ms ease-in-out;
    font-size: 1.25rem; }
    .nav-flex .nav-link:hover {
      background: #fff;
      color: #000; }

.nav-mobile {
  width: 100%;
  background: #dadad9;
  height: 100vh;
  position: absolute;
  margin-left: -100%;
  opacity: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  z-index: 999;
  transition: all 500ms ease-in-out; }
  .nav-mobile .nav-link {
    text-decoration: none;
    color: #000;
    width: 100%;
    text-align: center;
    padding: 1rem 0;
    font-weight: 700;
    letter-spacing: 1.3px;
    font-size: 1.25rem;
    border-bottom: 1px solid #000;
    transition: all 500ms ease-in-out; }
    .nav-mobile .nav-link:hover {
      background: #000;
      color: #dadad9; }
    .nav-mobile .nav-link:first-of-type {
      margin-top: 10vh;
      border-top: 1px solid #000; }
  .nav-mobile .wrapper {
    display: flex;
    margin-top: 5rem; }

.show-menu-mobile {
  opacity: 1;
  margin-left: 0; }

.no-scroll {
  overflow: hidden; }

.brand-logo {
  margin-right: auto; }

picture {
  width: 150px; }
  picture img {
    width: 150px; }

.hamburger-menu {
  width: 2rem;
  cursor: pointer;
  display: inline-block;
  transition: all 500ms ease-in-out; }
  .hamburger-menu .bar {
    background: #fff;
    width: 100%;
    height: 3px;
    margin: 6px 0;
    border-radius: 50px;
    transition: all 500ms ease-in-out; }
    .hamburger-menu .bar:first-child {
      width: 70%; }
    .hamburger-menu .bar:last-child {
      width: 70%;
      margin-left: auto; }

.animated .bar:first-child {
  transform: translateY(9px);
  background: #0abaee; }

.animated .bar:nth-child(2) {
  opacity: 0; }

.animated .bar:last-child {
  transform: translateY(-9px);
  background: #0abaee; }

#about {
  background: #fff;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  padding: 3rem 2rem; }
  #about .profile-picture {
    margin-top: 1rem;
    width: 250px;
    height: 250px;
    border-radius: 50%; }
  #about .personale-desc {
    font-size: 1rem;
    line-height: 1.3rem;
    letter-spacing: 1.2rem;
    padding-top: 2rem; }

@media screen and (min-width: 48rem) {
  #about {
    flex-direction: row !important; }
    #about .profile-picture {
      flex-basis: 100%; }
    #about .personal-desc {
      padding-left: 2rem; } }

#portfolio {
  background: url("../img/background2.svg");
  background-position: center center;
  background-size: cover;
  background-repeat: no-repeat;
  padding: 3rem 2rem;
  display: grid;
  grid-gap: 2rem;
  grid-template-columns: 1fr; }

@media screen and (min-width: 30rem) {
  #portfolio {
    grid-template-columns: repeat(2, 1fr) !important; } }

@media screen and (min-width: 48rem) and (orientation: landscape) {
  #portfolio {
    grid-template-columns: repeat(3, 1fr) !important; } }

@media screen and (min-width: 60rem) {
  #portfolio {
    grid-template-columns: repeat(2, 1fr) !important; } }

.project-card {
  background: #dadad9;
  border-radius: 5px;
  box-shadow: 0 5px 10px 0 rgba(33, 33, 33, 0.7);
  transition: all 500ms ease-in-out; }
  .project-card:hover {
    box-shadow: 0 7px 13px 0 rgba(33, 33, 33, 0.7);
    transform: scale(1.025); }
  .project-card:hover .fa-arrow-right {
    animation: arrow-animation 1.5s infinite; }
  .project-card .project-image {
    width: 100%;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px; }
    .project-card .project-image img {
      width: 100%; }
  .project-card .project-text {
    padding: 0 1.25rem 1.25rem 1.25rem;
    color: #000; }
    .project-card .project-text .project-title {
      font-size: 1.25rem;
      letter-spacing: 1.2px;
      line-height: 1.5rem; }
    .project-card .project-text .project-description {
      font-size: 1rem;
      letter-spacing: 1px;
      opacity: 0.7;
      line-height: 1.25rem; }
    .project-card .project-text .project-link {
      text-decoration: none;
      font-size: 1.05rem;
      letter-spacing: 1px;
      color: #000;
      display: flex;
      align-items: center;
      transition: all 500ms ease-in-out; }
      .project-card .project-text .project-link .fa-arrow-right {
        padding-left: 10px; }

@keyframes arrow-animation {
  0% {
    transform: none; }
  50% {
    transform: translateX(20px); }
  100% {
    transform: none; } }

#contact {
  background: linear-gradient(65deg, #000 -5%, #dadad9 125%);
  padding: 3rem 2rem;
  color: #fff; }
  #contact .form {
    display: flex;
    flex-direction: column; }
    #contact .form label {
      padding: 20px 0 5px 0;
      font-weight: 700;
      letter-spacing: 1.2px;
      text-transform: uppercase;
      font-size: 1rem; }
    #contact .form input,
    #contact .form textarea {
      padding: 7px 3px;
      letter-spacing: 1px; }
      #contact .form input::placeholder,
      #contact .form textarea::placeholder {
        opacity: 0.7; }
    #contact .form .submit-btn {
      width: 220px;
      height: 40px;
      background: #0abaee;
      cursor: pointer;
      border: 1px solid #0abaee;
      text-transform: uppercase;
      border-radius: 5px;
      color: #fff;
      margin: 1.25rem auto 1rem auto;
      transition: all 500ms ease-in-out; }
      #contact .form .submit-btn:hover {
        background: #0894bd;
        transform: translateY(-2px); }

@media screen and (min-width: 48rem) {
  .form {
    width: 80vw !important;
    margin: 0 auto; } }

