.footer {
  background: $tertiaryColor;
  color: $primaryColor;
  padding: $gutterWidth;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-end; }

.footer-links-container {
  width: 100%;
  display: flex;
  justify-content: center;
  align-self: center; }

.footer-links {
  text-align: center;
  padding-top: 2rem; }

.footer-link {
  text-decoration: none;
  color: lighten($primaryColor, 50);
  font-size: 2rem;
  padding: 0 10px; }

.fa-github {
  @include transition;
  &:hover {
    color: $github;
    transform: scale(1.2); } }

.fa-twitter {
  @include transition;
  &:hover {
    color: $twitter;
    transform: scale(1.2); } }

.fa-linkedin-in {
  @include transition;
  &:hover {
    color: $linkedin;
    transform: scale(1.2); }


  .copyright {
    padding: 0; } }
